* {
  margin: 0;
  padding: 0;
}

html, 
body {
  font-size: 1.5em;
  background-image: url(../src/imagenes/libreria.jpeg);
  scroll-behavior: smooth;
  min-height: 100%;
  min-width: 1024px;
	background-size: cover;
}


h1 {
  width: 1170px;
  height: 50px;
  font-size: 34px;
  font-family: 'Georgia','Courier New', Courier, monospace;
  font-weight: 700;
  line-height: 1.1;
  margin: 40px 0;
  color: rgb(39, 36, 36);
  border: 1px solid black;
  border-radius: 10px;
  padding-top: 20px;
  background-color: rgba(60, 46, 46, 0.7);
}

h1:hover {
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
  }

.App {
  min-height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  text-align: center;

}

.contenedor-principal {
  min-height: 600px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}