.contenedor-testimonio {
    width: 1170px;
    height: 368px;
    margin: 10px 10px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 3px 13px 1px rgb(0 0 0 / 9%);
    border: 1px solid black;
  border-radius: 10px;
  background-color: rgba(60, 46, 46, 0.7);
  }

  .contenedor-testimonio:hover {
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
    }
  
  .imagen-testimonio {
    width: 550px;
    height: auto;
    display: flex;
  }
  
  .contenedor-texto-testimonio {
    text-align: center;
    padding: 30px;
    font-family: Bookman;
    font-weight: 600;
    line-height: 1.5rem;
    color: rgb(206, 182, 182);
  }
  
  .nombre-testimonio {
    font-size: 1.3rem;
    color:darksalmon;
  }
  
  .cargo-testimonio {
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 30px;
    color:rgba(4, 107, 185, 0.864)
  }
  
  .texto-testimonio {
    font-size: 1.1rem;
    text-align: justify;
  }

  .footer {
    color:bisque;
  }